@import '../../main/assets/main.scss';

.verify_forgot{
  .verify_forgot_form{
	display: flex;
	flex-direction: column;
	align-items: center;
	.character{
	  box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.10);
	  background-color: #fff;
	  border: none;
	  border-radius: 0.5rem;
	  height: 3.75rem;
	  display: flex;
	  align-items: center;
	  justify-content: center;
	}
	.container{
	  display: flex;
	  justify-content: center;
	  margin: 30px 0;
	}
	.character_selected{
	  outline: none;
	  border: 1px solid $base-color;
	  color: #000;
	}
	.verify_button{
	  padding: $p-md 0;
	  width: 100%;
	  font-size: $font-sm;
	}
  }
}